
.crafting-category-bar {

	width:  10%;
	height: 70vh;
	background-color: rgba(14, 56, 125, 0.9);
	border-radius: 5px;

}

.crafting-pictures {

	width: 76px;
	height: 76px;
	margin-top: 5%;
	border-style: solid;
	border-color: rgba(24, 81, 173, 0.9);
	border-width: 1px;
	border-radius: 10px;

}

.crafting-pictures-container {

	margin-left: 18%;

}

.crafting-titles {

	font-size: 36px;
	color: white;
	margin-left: 1%;

}

.recipe-container {

	width: 10%;
	margin-left: 1%;
	margin-top: 1%;
	border-radius: 10px;
	background-color: rgba(84, 82, 76, 0.9);
	color: white;

}

.columns {

	width: 94%;
	margin-left: 1% !important;

}

.column {

	margin-top: 1%;
	margin-right: 1%;
	color: black;
	background-color: white;
	border-radius: 10px;

}

.column:hover {

	color: black;
	background-color: rgb(200, 200, 200);
	
}

.tooltip {
    background-color: rgb(15, 52, 112);
    color: white;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 13px;
    margin-top: 0.5%;
}

.tooltip::after {
  content: "";
  position: absolute;
  top: -14%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent rgb(15, 52, 112) transparent;
}

.modal_buttons {

	margin-top: 2%;
	margin-left: 1%;

}

.input {

	margin-top: 2%;

}

.modal-content {

	background-color: white !important;
	height: 20%;

}